import React, { Component } from "react";
import axios from "axios";
import {
  PostWrapper,
  Post,
  HomeArchive,
  Calandar,
  Equipment,
  Classroom,
  LogoImage,
} from "../../components";
import { ArchiveWrapper } from "../../components";
import "./ContentContainer.css";

class ContentContainer extends Component {
  state = {
    postId: 1,
    loadingPost: false,
    loadingArchive: false,
    postList: [],
    archiveList: [],
    items: 20,
    preItems: 0,
  };

  UNSAFE_componentWillMount() {}

  componentDidMount() {
    // this.fetchPostInfo(1);
    this._loadPost();
    this._loadArchive();
    // window.addEventListener('scroll', this._infiniteScroll);
  }

  //   componentWillUnmount() {
  // 	  window.removeEventListener("scroll", this.infiniteScroll);
  //   }

  _loadPost = async () => {
    axios
      .get("http://127.0.0.1:8000/api/v1/postings")
      .then(({ data }) => {
        this.setState({
          ...this.state,
          loadingPost: true,
          postList: data,
        });
      })
      .catch((e) => {
        console.error(e);
        this.setState({
          ...this.state,
          loadingPost: false,
        });
      });
  };

  _loadArchive = async () => {
    axios
      .get("https://jsonplaceholder.typicode.com/photos")
      .then(({ data }) => {
        this.setState({
          ...this.state,
          loadingArchive: true,
          archiveList: data,
        });
      })
      .catch((e) => {
        console.error(e);
        this.setState({
          ...this.state,
          loadingArchive: false,
        });
      });
  };

  render() {
    const { postList, archiveList } = this.state;
    const latestArchiveList = archiveList.slice(0, 6);
    const latestPostList = postList.slice(0, 20);

    return (
      <div className="contentcontainer">
        <PostWrapper>
          <LogoImage></LogoImage>
          {latestPostList &&
            latestPostList.map((post) => {
              return (
                <Post
                  key={post.pk}
                  title={post.title}
                  date={post.created}
                  category={post.category}
                  id={post.pk}
                ></Post>
              );
            })}
          {/* <Classroom></Classroom>
          <Calandar
            onClick={() => console.log("Calandar Module Clicked!")}
          ></Calandar>
          <Equipment></Equipment> */}
        </PostWrapper>
        <ArchiveWrapper>
          {latestArchiveList &&
            latestArchiveList.map((post) => {
              return (
                <HomeArchive
                  key={post.id}
                  title={post.title}
                  id={post.id}
                  thumbnailUrl={post.thumbnailUrl}
                ></HomeArchive>
              );
            })}
        </ArchiveWrapper>
      </div>
    );
  }
}

export default ContentContainer;
